$mainColor: #17daa3;
$navColor: rgba(0, 0, 0, 0);
$logoColor: rgb(255, 255, 255);
$logoColor_hover: $mainColor;
$navItemColor: rgb(255, 255, 255);
$navItemColor_hover: $mainColor;
$burgerColor: rgb(255, 255, 255);
$MQ_menuColor: rgba(0, 0, 0, 0.856);
$height: 55px;
$width: 1300px;
.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: $navColor;
  &:before {
    content: "";
    width: 100vw;
    position: absolute;
    background-color: $navColor;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 90;
    // box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.5);
    height: 50px;
  }
}

.wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: $width;
  height: $height;
  padding: 0 10px;
  z-index: 999;
  &-mq {
    display: flex;
    align-items: center;
  }
}

.navBlack {
  position: absolute;
  height: 0px;
  opacity: 0;
  width: 100vw;
  top: 0;
  transition: all 0.3s ease;
  background-color: rgba(0, 0, 0, 0.925);
  // animation: navAnim 0.2s ease;
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: $logoColor;
  z-index: 99;
  align-items: center;
  transition: color 0.25s ease;
  &:after {
    content: ".";
    color: $mainColor;
  }
  &:hover {
    color: $logoColor_hover;
  }
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  z-index: 91;
  align-items: center;
  text-align: center;
  height: $height;
  &__item {
    &:not(:first-child) {
      margin-left: 30px;
    }
    &-inner {
      text-decoration: none;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: 700;
      color: $navItemColor;
      transition: all 0.3s ease;
      cursor: pointer;
      &:last-child {
        padding: 5px;
      }
      &:hover {
        color: $navItemColor_hover;
      }
    }
  }
}

.search {
  border: none;
  background-color: transparent;
  font-size: 15px;
  border-bottom: 2px solid rgb(255, 255, 255);
  border-radius: 1px;
  transition: border-color, 0.3s ease;
  width: 130px;
  color: white;
  user-select: none;
  text-align: center;
  &:hover {
    border-color: $mainColor;
  }
  &-hidden {
    // display: none;
    width: 0;
    user-select: none;
    margin-left: 0px;
  }
  @media only screen and (max-width: 767px) {
    font-size: 18px;
    margin-left: 25px;
    &-hidden {
      width: 0;
      margin-left: 0px;
    }
  }
}

.searchBtn {
  color: white;
  font-size: 14px;
  font-weight: bold;
  &:hover ~ .search {
    border-color: $mainColor;
  }
}

@media only screen and (max-width: 767px) {
  .logo {
    font-size: 30px;
  }
  .scrollLock {
    overflow: hidden !important;
  }
  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 99;
    margin: 0;
    cursor: pointer;
  }
  .active:before {
    transform: rotate(45deg);
    top: 12px !important;
  }
  .active:after {
    transform: rotate(-45deg);
    bottom: 10px !important;
  }
  .active > span {
    transform: scale(0);
  }
  .burger:before,
  .burger:after,
  .burger > span {
    content: "";
    background-color: $burgerColor;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 100px;
  }
  .burger > span {
    top: 11px;
    transition: all 0.2s ease-in-out;
  }
  .burger:before {
    top: 0;
    transition: all 0.35s ease-in-out;
  }
  .burger:after {
    bottom: 0;
    transition: all 0.35s ease-in-out;
  }
  .wrapper-mq {
    justify-content: space-between;
    background-color: $navColor;
    padding: 5px 5px 0;
    @media only screen and (max-width: 767px) {
      padding-top: 0;
    }
    z-index: 999;
    width: 100%;
  }
  .navCont {
    position: fixed;
    top: -100%;
    left: 0;
    max-width: 100%;
    width: 100%;
    height: 100%;
    background: $MQ_menuColor;
    padding-top: 40px;
    z-index: 9;
    display: flex;
    justify-content: center;
    transition: all 0.6s ease;
    overflow: auto;

    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    scrollbar-width: none;
    /* Firefox */
  }
  .navCont::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
  }
  .active {
    top: 0;
  }
  .navItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: center;
    width: 100%;
    height: 100%;
    top: -40px;
    &__item {
      margin-left: 0 !important;
      margin-top: 40px;
      &:last-child {
        margin-bottom: 40px;
      }
      &:first-child {
        margin-top: 80px;
      }
      &-inner {
        font-size: 16px;
      }
    }
  }
}
