.stars {
  position: absolute;
  width: 100vw;
  height: 100%;
  left: 50%;
  transform: translate(-50%);
  z-index: -1;
}

.larrow,
.rarrow {
  transition: all 0.3s ease;
  padding: 5px;
  cursor: pointer;
  &:hover {
    transform: scale(1.15);
  }
  @media only screen and (max-width: 767px) {
    display: none;
  }
  @media only screen and (max-height: 500px) {
    display: none;
  }
}

.barrow {
  position: absolute;
  bottom: 3%;
  left: 50%;
  transform: translate(-50%);
  padding: 3px;
  cursor: pointer;
  @media only screen and (max-width: 767px) {
    display: none;
  }
  @media only screen and (max-height: 500px) {
    display: none;
  }
}

.slick-arrow {
  position: absolute;
  cursor: pointer;
  z-index: 0;
  @media only screen and (max-width: 767px) {
    display: none;
  }
}

.slick-next {
  top: 50%;
  right: 5%;
  z-index: 1;
}

.slick-prev {
  top: 50%;
  left: 5%;
  z-index: 1;
}

.fa-angle-right,
.fa-angle-left {
  color: white;
  font-size: 36px;
  transition: all 0.2s ease;
  z-index: 0;
  &:hover {
    color: $mainColor;
    transform: scale(1.2);
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 1rem 0;
  list-style-type: none;
  position: absolute;
  bottom: 12%;
  left: 50%;
  @media only screen and (max-width: 767px) {
    bottom: 5%;
  }
  transform: translate(-50%);
  li {
    margin: 0 7px;
  }
  button {
    cursor: pointer;
    display: block;
    width: 12px;
    height: 12px;
    padding: 0;
    border: none;
    border-radius: 100%;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid rgb(117, 117, 117);
    text-indent: -9999px;
    transition: all 0.2s ease;
    &:hover {
      transform: scale(1.2);
    }
  }
  li.slick-active button {
    background-color: $mainColor;
    border: none;
  }
}

.textCont {
  max-width: 1300px;
  margin: 50px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 15px;
}

.slide {
  position: relative;
  //  max-width: 1300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  @include btn;
  padding: 12px 36px;
  text-align: center;
  margin: 40px auto;
  border-radius: 50px;
  background-color: transparent;
  border: 1px solid rgb(202, 202, 202);
  font-size: 16px;
  @media only screen and (max-height: 500px) {
    margin-top: 15px;
  }
  &:hover {
    box-shadow: 0px 0px 30px 1px rgba(255, 255, 255, 0.486);
    transform: scale(1.06);
    border-color: white;
  }
  &-big {
    font-size: 20px;
    padding: 20px 50px;
  }

  &-black {
    margin-top: 25px !important;
    margin-bottom: 10px;
  }
  &-green {
    background-color: darken($color: $mainColor, $amount: 10);
    border: 1px solid $mainColor;
    border-color: darken($color: $mainColor, $amount: 10);
    margin-top: 80px !important;
    &:hover {
      //box-shadow: 0px 0px 30px 1px rgba(98, 212, 147, 0.404);
      box-shadow: 0px 0px 30px 1px rgba(0, 255, 200, 0.452);
      color: white;
      background-color: $mainColor;
      transform: scale(1.06);
      border: 1px solid $mainColor;
    }
    @media only screen and (max-width: 950px) {
      margin: 60px auto 0 !important;
    }
  }
}

.title {
  font-size: 45px;
  margin-bottom: 50px;
  color: rgb(218, 218, 218);
  text-align: center;
  &-slide {
    color: white;
    position: relative;
    margin: 0;
    font-size: 4vw;
    font-weight: 100;
    width: 100vw;
    max-width: 80vw;
    @media only screen and (max-width: 1920px) {
      font-size: 6vw;
    }
    @media only screen and (max-width: 767px) {
      font-size: 10vw;
    }
  }
  @media only screen and (max-height: 500px) {
    font-size: 5vw;
  }
  &-product {
    font-size: 30px;
    margin: 30px 0 20px;
    z-index: 2;
  }
  &-info {
    padding: 0;
    margin: 0;
  }
  &-line {
    position: relative;
    @media only screen and (max-width: 767px) {
      font-size: 28px;
      margin-bottom: 50px;
    }
    &:after {
      content: "";
      width: 25px;
      height: 2px;
      position: absolute;
      background-color: $mainColor;
      top: 145%;
      left: 50%;
      transform: translate(-50%);
    }
  }
}

.slideText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}

.textWrap {
  text-align: center;
  margin: 0 auto 100px;
  max-width: 80%;
  width: 1200px;
  @media only screen and (max-width: 767px) {
    max-width: 90%;
    margin: 0 auto 60px;
  }
}

.subtitle {
  color: rgb(185, 185, 185);
  text-align: center;
  font-size: 16px;
  z-index: 2;
  @media only screen and (max-width: 767px) {
    font-size: 14px;
  }
  &-info {
    max-width: 60px !important;
    //margin: 20px 0;
    padding: 0 20px;
    color: darken($color: #dce0e6, $amount: 10);
  }
  &-slide {
    position: relative;
    color: rgb(204, 204, 204);
    margin-top: 10px;
    width: 90vw;
    max-width: 900px;
    font-weight: 100;
    text-align: center;
    margin: 20px auto;
    @media only screen and (max-width: 1150px) {
      width: 75vw;
    }
  }
}

.products {
  max-width: 1300px;
  margin: 20px auto 50px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  @media only screen and (max-width: 800px) {
    padding: 0;
    margin-top: 0;
  }
  &__items {
    width: 100%;
    justify-content: center;
    @include row-flex();
    @include md(justify-content, center);
    margin: 0;
  }
}

.fix {
  // transition: all 0.8s ease;
  animation: opFix 0.9s ease-in-out;
}

@keyframes opFix {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.item {
  @include col();
  @include size(3);
  @include size-md(5);
  @include size-xs(10);
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  text-align: center;
  padding: 50px 20px;
  background-color: rgb(51, 50, 50);
  transition: all 0.3s ease;
  cursor: pointer;
  min-width: 250px;
  z-index: 4;
  &:hover {
    box-shadow: 0px 0px 30px 1px rgba(0, 255, 200, 0.363);
    background-color: darken($color: $mainColor, $amount: 2);
    border-radius: 25px;
    transform: scale(1.05);
  }
}

.item:hover > .icon {
  background-color: rgb(255, 255, 255);
}

.item:hover > .icon > .fa-product:before {
  color: darken($color: $mainColor, $amount: 2);
}

.item:hover > .title-product {
  color: rgb(255, 255, 255);
}

.item:hover > .subtitle-product {
  color: rgb(255, 255, 255);
}

.item:hover ~ .img-star {
  width: 150px;
}

.fa {
  &-product {
    z-index: 1;
    &:before {
      font-size: 50px;
      color: rgb(99, 99, 99);
      transition: all 0.3s ease;
    }
  }
}

.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 140px;
  padding: 30px;
  border-radius: 50%;
  background-color: #272727;
  transition: all 0.4s ease;
  cursor: pointer;
  z-index: 0;
}

// .icon:hover .fa-aboutus:before {
//     color: white;
// }
.text {
  font-size: 45px;
  margin-bottom: 70px;
  color: rgb(218, 218, 218);
  &-contact {
    margin-bottom: 70px;
    text-align: center;
  }
  &-info {
    color: darken($color: #dce0e6, $amount: 10);
  }
}

.wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1300px;
  flex-direction: column;
  width: 100vw;
  padding: 60px 20px;
  @media only screen and (max-width: 767px) {
    padding: 40px 20px;
  }
}

.info {
  display: flex;
  justify-content: center;
  margin: 30px auto 0;
  width: 100%;
  background-color: rgb(8, 8, 8);
  border-radius: 20px;
  clip: rect(30px, auto, 40px, 0px);
  height: 100%;
  align-items: center;
}

//////////@include
.form {
  max-width: 780px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}

.input {
  margin: 7px 0;
  padding: 10px 20px;
  font-size: 20px;
  max-width: 90%;
  min-width: 80%;
  text-align: center;
  border: none;
  color: white;
  background-color: rgb(41, 41, 41);
  transition: background-color 0.25s ease;
  border-radius: 50px;
  &:hover {
    background-color: rgb(61, 61, 61);
  }
  @media screen and (max-width: 960px) {
    font-size: 16px;
  }
}

.input::placeholder {
  color: rgb(105, 105, 105);
}

.textarea {
  //  padding: 10px;
  margin: 7px 0;
  max-width: 90%;
  min-width: 80%;
  resize: none;
  font-size: 20px;
  border: none;
  color: rgb(233, 233, 233);
  text-align: center;
  background-color: rgb(41, 41, 41);
  border-radius: 25px;
  transition: background-color 0.25s ease;
  padding-top: 10px;
  &:hover {
    background-color: rgb(61, 61, 61);
  }
  @media screen and (max-width: 960px) {
    font-size: 16px;
  }
}

.textarea::placeholder {
  color: rgb(105, 105, 105);
  // padding-top: 10px;
}

.contact {
  padding-top: 50px;
  background-color: black;
}

.footer {
  text-align: center;
  padding-bottom: 15px;
}
