html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
menu,
ol,
pre,
ul,
figure,
dl,
dd,
body,
blockquote {
  margin: 0;
  padding: 0;
}

body {
  color: black;
}

a {
  color: black;
}

a:visited:active {
  color: transparent;
}

input,
button {
  background-color: white;
  border: 1px solid black;
}

.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0);
}

.nav:before {
  content: "";
  width: 100vw;
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  height: 100%;
  top: 0;
  left: 0;
  z-index: 90;
  height: 50px;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1300px;
  height: 55px;
  padding: 0 10px;
  z-index: 999;
}

.wrapper-mq {
  display: flex;
  align-items: center;
}

.navBlack {
  position: absolute;
  height: 0px;
  opacity: 0;
  width: 100vw;
  top: 0;
  transition: all 0.3s ease;
  background-color: rgba(0, 0, 0, 0.925);
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: white;
  z-index: 99;
  align-items: center;
  transition: color 0.25s ease;
}

.logo:after {
  content: ".";
  color: #17daa3;
}

.logo:hover {
  color: #17daa3;
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  z-index: 91;
  align-items: center;
  text-align: center;
  height: 55px;
}

.navItems__item:not(:first-child) {
  margin-left: 30px;
}

.navItems__item-inner {
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  color: white;
  transition: all 0.3s ease;
  cursor: pointer;
}

.navItems__item-inner:last-child {
  padding: 5px;
}

.navItems__item-inner:hover {
  color: #17daa3;
}

.search {
  border: none;
  background-color: transparent;
  font-size: 15px;
  border-bottom: 2px solid white;
  border-radius: 1px;
  transition: border-color, 0.3s ease;
  width: 130px;
  color: white;
  user-select: none;
  text-align: center;
}

.search:hover {
  border-color: #17daa3;
}

.search-hidden {
  width: 0;
  user-select: none;
  margin-left: 0px;
}

.searchBtn {
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.searchBtn:hover ~ .search {
  border-color: #17daa3;
}

* {
  font-family: sans-serif;
}

