.stars {
  position: absolute;
  width: 100vw;
  height: 100%;
  left: 50%;
  transform: translate(-50%);
  z-index: -1;
}

.larrow,
.rarrow {
  transition: all 0.3s ease;
  padding: 5px;
  cursor: pointer;
}

.larrow:hover,
.rarrow:hover {
  transform: scale(1.15);
}

.barrow {
  position: absolute;
  bottom: 3%;
  left: 50%;
  transform: translate(-50%);
  padding: 3px;
  cursor: pointer;
}

.slick-arrow {
  position: absolute;
  cursor: pointer;
  z-index: 0;
}

.slick-next {
  top: 50%;
  right: 5%;
  z-index: 1;
}

.slick-prev {
  top: 50%;
  left: 5%;
  z-index: 1;
}

.fa-angle-right,
.fa-angle-left {
  color: white;
  font-size: 36px;
  transition: all 0.2s ease;
  z-index: 0;
}

.fa-angle-right:hover,
.fa-angle-left:hover {
  color: #17daa3;
  transform: scale(1.2);
}

.slick-dots {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 1rem 0;
  list-style-type: none;
  position: absolute;
  bottom: 12%;
  left: 50%;
  transform: translate(-50%);
}

.slick-dots li {
  margin: 0 7px;
}

.slick-dots button {
  cursor: pointer;
  display: block;
  width: 12px;
  height: 12px;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #757575;
  text-indent: -9999px;
  transition: all 0.2s ease;
}

.slick-dots button:hover {
  transform: scale(1.2);
}

.slick-dots li.slick-active button {
  background-color: #17daa3;
  border: none;
}

.textCont {
  max-width: 1300px;
  margin: 50px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 15px;
}

.slide {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  padding: 10px 30px;
  transition: all 0.25s ease;
  background-color: black;
  color: white;
  cursor: pointer;
  padding: 12px 36px;
  text-align: center;
  margin: 40px auto;
  border-radius: 50px;
  background-color: transparent;
  border: 1px solid #cacaca;
  font-size: 16px;
}

.btn:hover {
  background-color: white;
  color: black;
}

.btn:hover {
  box-shadow: 0px 0px 30px 1px rgba(255, 255, 255, 0.486);
  transform: scale(1.06);
  border-color: white;
}

.btn-big {
  font-size: 20px;
  padding: 20px 50px;
}

.btn-black {
  margin-top: 25px !important;
  margin-bottom: 10px;
}

.btn-green {
  background-color: #12ac81;
  border: 1px solid #17daa3;
  border-color: #12ac81;
  margin-top: 80px !important;
}

.btn-green:hover {
  box-shadow: 0px 0px 30px 1px rgba(0, 255, 200, 0.452);
  color: white;
  background-color: #17daa3;
  transform: scale(1.06);
  border: 1px solid #17daa3;
}

.title {
  font-size: 45px;
  margin-bottom: 50px;
  color: #dadada;
  text-align: center;
}

.title-slide {
  color: white;
  position: relative;
  margin: 0;
  font-size: 4vw;
  font-weight: 100;
  width: 100vw;
  max-width: 80vw;
}

.title-product {
  font-size: 30px;
  margin: 30px 0 20px;
  z-index: 2;
}

.title-info {
  padding: 0;
  margin: 0;
}

.title-line {
  position: relative;
}

.title-line:after {
  content: "";
  width: 25px;
  height: 2px;
  position: absolute;
  background-color: #17daa3;
  top: 145%;
  left: 50%;
  transform: translate(-50%);
}

.slideText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}

.textWrap {
  text-align: center;
  margin: 0 auto 100px;
  max-width: 80%;
  width: 1200px;
}

.subtitle {
  color: #b9b9b9;
  text-align: center;
  font-size: 16px;
  z-index: 2;
}

.subtitle-info {
  max-width: 60px !important;
  padding: 0 20px;
  color: #bec6d1;
}

.subtitle-slide {
  position: relative;
  color: #cccccc;
  margin-top: 10px;
  width: 90vw;
  max-width: 900px;
  font-weight: 100;
  text-align: center;
  margin: 20px auto;
}

.products {
  max-width: 1300px;
  margin: 20px auto 50px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.products__items {
  width: 100%;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: 0;
}

.fix {
  animation: opFix 0.9s ease-in-out;
}

@keyframes opFix {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  text-align: center;
  padding: 50px 20px;
  background-color: #333232;
  transition: all 0.3s ease;
  cursor: pointer;
  min-width: 250px;
  z-index: 4;
}

.item:hover {
  box-shadow: 0px 0px 30px 1px rgba(0, 255, 200, 0.363);
  background-color: #16d19c;
  border-radius: 25px;
  transform: scale(1.05);
}

.item:hover > .icon {
  background-color: white;
}

.item:hover > .icon > .fa-product:before {
  color: #16d19c;
}

.item:hover > .title-product {
  color: white;
}

.item:hover > .subtitle-product {
  color: white;
}

.item:hover ~ .img-star {
  width: 150px;
}

.fa-product {
  z-index: 1;
}

.fa-product:before {
  font-size: 50px;
  color: #636363;
  transition: all 0.3s ease;
}

.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 140px;
  padding: 30px;
  border-radius: 50%;
  background-color: #272727;
  transition: all 0.4s ease;
  cursor: pointer;
  z-index: 0;
}

.text {
  font-size: 45px;
  margin-bottom: 70px;
  color: #dadada;
}

.text-contact {
  margin-bottom: 70px;
  text-align: center;
}

.text-info {
  color: #bec6d1;
}

.wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1300px;
  flex-direction: column;
  width: 100vw;
  padding: 60px 20px;
}

.info {
  display: flex;
  justify-content: center;
  margin: 30px auto 0;
  width: 100%;
  background-color: #080808;
  border-radius: 20px;
  clip: rect(30px, auto, 40px, 0px);
  height: 100%;
  align-items: center;
}

.form {
  max-width: 780px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}

.input {
  margin: 7px 0;
  padding: 10px 20px;
  font-size: 20px;
  max-width: 90%;
  min-width: 80%;
  text-align: center;
  border: none;
  color: white;
  background-color: #292929;
  transition: background-color 0.25s ease;
  border-radius: 50px;
}

.input:hover {
  background-color: #3d3d3d;
}

.input::placeholder {
  color: dimgray;
}

.textarea {
  margin: 7px 0;
  max-width: 90%;
  min-width: 80%;
  resize: none;
  font-size: 20px;
  border: none;
  color: #e9e9e9;
  text-align: center;
  background-color: #292929;
  border-radius: 25px;
  transition: background-color 0.25s ease;
  padding-top: 10px;
}

.textarea:hover {
  background-color: #3d3d3d;
}

.textarea::placeholder {
  color: dimgray;
}

.contact {
  padding-top: 50px;
  background-color: black;
}

.footer {
  text-align: center;
  padding-bottom: 15px;
}

@media only screen and (max-width: 1920px) {
  .title-slide {
    font-size: 6vw;
  }
}

@media only screen and (max-width: 1150px) {
  .subtitle-slide {
    width: 75vw;
  }
}

@media screen and (max-width: 960px) {
  .products__items {
    justify-content: center;
  }

  .item {
    width: calc(41.66667% - 30px);
  }

  .input {
    font-size: 16px;
  }

  .textarea {
    font-size: 16px;
  }
}

@media only screen and (max-width: 950px) {
  .btn-green {
    margin: 60px auto 0 !important;
  }
}

@media only screen and (max-width: 800px) {
  .products {
    padding: 0;
    margin-top: 0;
  }
}

@media only screen and (max-width: 767px) {
  .search {
    font-size: 18px;
    margin-left: 25px;
  }

  .search-hidden {
    width: 0;
    margin-left: 0px;
  }

  .logo {
    font-size: 30px;
  }

  .scrollLock {
    overflow: hidden !important;
  }

  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 99;
    margin: 0;
    cursor: pointer;
  }

  .active:before {
    transform: rotate(45deg);
    top: 12px !important;
  }

  .active:after {
    transform: rotate(-45deg);
    bottom: 10px !important;
  }

  .active > span {
    transform: scale(0);
  }

  .burger:before,
  .burger:after,
  .burger > span {
    content: "";
    background-color: white;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 100px;
  }

  .burger > span {
    top: 11px;
    transition: all 0.2s ease-in-out;
  }

  .burger:before {
    top: 0;
    transition: all 0.35s ease-in-out;
  }

  .burger:after {
    bottom: 0;
    transition: all 0.35s ease-in-out;
  }

  .wrapper-mq {
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0);
    padding: 5px 5px 0;
    z-index: 999;
    width: 100%;
  }

  .navCont {
    position: fixed;
    top: -100%;
    left: 0;
    max-width: 100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.856);
    padding-top: 40px;
    z-index: 9;
    display: flex;
    justify-content: center;
    transition: all 0.6s ease;
    overflow: auto;
    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    scrollbar-width: none;
    /* Firefox */
  }

  .navCont::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
  }

  .active {
    top: 0;
  }

  .navItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: center;
    width: 100%;
    height: 100%;
    top: -40px;
  }

  .navItems__item {
    margin-left: 0 !important;
    margin-top: 40px;
  }

  .navItems__item:last-child {
    margin-bottom: 40px;
  }

  .navItems__item:first-child {
    margin-top: 80px;
  }

  .navItems__item-inner {
    font-size: 16px;
  }

  .larrow,
  .rarrow {
    display: none;
  }

  .barrow {
    display: none;
  }

  .slick-arrow {
    display: none;
  }

  .slick-dots {
    bottom: 5%;
  }

  .title-slide {
    font-size: 10vw;
  }

  .title-line {
    font-size: 28px;
    margin-bottom: 50px;
  }

  .textWrap {
    max-width: 90%;
    margin: 0 auto 60px;
  }

  .subtitle {
    font-size: 14px;
  }

  .wrap {
    padding: 40px 20px;
  }
}

@media only screen and (max-width: 767px) and (max-width: 767px) {
  .wrapper-mq {
    padding-top: 0;
  }
}

@media screen and (max-width: 560px) {
  .item {
    width: calc(83.33333% - 30px);
  }
}

@media only screen and (max-height: 500px) {
  .larrow,
  .rarrow {
    display: none;
  }

  .barrow {
    display: none;
  }

  .btn {
    margin-top: 15px;
  }

  .title {
    font-size: 5vw;
  }
}